import React, { useState, useEffect, useRef } from 'react'

//component
import Button from './Button/Button'
import {isOperator, calculate} from "./Calculations/Calculations"
import ButtonClick from "./Asset/ButtonClick2.mp3"

// classes 
import classes from "./Calculator.css"

const buttonClicked = new Audio(ButtonClick);

const Calculator = (props) => {

    const [lastNumber, setLastNumber] = useState(null)
    const [operationArray, setOperationArray] = useState([])
    const [result, setResult] = useState(0)
    
    const operationRef = useRef(null)

    useEffect(() => {
        // console.log(operationArray)
        operationRef.current.scrollTo(0, operationRef.current.scrollHeight)

        if(lastNumber){
            if(isOperator(operationArray[0])){
                // console.log(calculate([...operationArray]))
                setResult("Invalid Operation")
            }else{
                setResult(calculate(operationArray) ? calculate(operationArray) : 0)
            }
        }
        if(!operationArray.length){
            setResult(0)
        }

    }, [operationArray])

    useEffect(()=> {


        window.addEventListener("keypress", keyPressHandler)
        window.addEventListener("keydown", keyDownHandler)

        return()=>{
            window.removeEventListener("keypress", keyPressHandler)
            window.removeEventListener("keydown", keyDownHandler)
        }
 
    }, [operationArray, result, lastNumber]) 

    const keyPressHandler = (e) => {
        let keyPress = e.key === "/" ? "Ã·" : (e.key === "*" ? "Ã—" : (e.key === "Delete" ? "AC" : (e.key === "Enter" ? "=":e.key)));
        clickedHandler(keyPress+"")
    }

    const keyDownHandler = (e) => {
        let keyPress = (e.key === "Backspace" ? "back" : null);
        if(keyPress){
            clickedHandler(keyPress)
        }
    }
    
    
    const clickedHandler = (clickedButton) => {
        console.log(clickedButton, typeof(clickedButton))
        buttonClicked.pause()
        buttonClicked.currentTime = 0

        if(clickedButton === "="){
            console.log(result)
            setOperationArray([result])
            setLastNumber(result)
        }
        else if(clickedButton === "+/-"){
            if(lastNumber === null){
                return;
            }
            setLastNumber((-1*parseFloat(lastNumber))+"")
            let tempOperationArray = [...operationArray]
            tempOperationArray.pop()
            tempOperationArray.push((-1*parseFloat(lastNumber))+"")
            setOperationArray(tempOperationArray)
        }
        else if(clickedButton === "."){
            
            let tempLastNumber = lastNumber;
            let tempOperationArray = [...operationArray]
            if(tempLastNumber){
                if(tempLastNumber.indexOf(".") === -1){
                    tempLastNumber += "."
                    tempOperationArray.pop()
                }else{
                    return
                }
            }
            else{
                tempLastNumber = "0."
            }
            setLastNumber(tempLastNumber)
            setOperationArray([...tempOperationArray, tempLastNumber])
        }
        else if(isOperator(clickedButton)){
            // if clicked button is an operator 
            if(lastNumber === null){
                if(isOperator(operationArray[operationArray.length-1])){
                    let tempOperationArray = [...operationArray]
                    tempOperationArray.pop()
                    tempOperationArray.push(clickedButton)
                    setOperationArray(tempOperationArray)
                }
                else{
                    setOperationArray([...operationArray, clickedButton])
                }

            }else{   
                setOperationArray([...operationArray, clickedButton])
                setLastNumber(null)
            }
        }
        else if(!isNaN(parseInt(clickedButton))){
            // if clicked button is number 
            if(lastNumber === null){
                setLastNumber(clickedButton)
                setOperationArray([...operationArray, clickedButton])
            }else{
                let finalNumber = (lastNumber+"")+(clickedButton)
                setLastNumber(finalNumber)
                let tempOperationArray = [...operationArray]
                tempOperationArray.pop()
                tempOperationArray.push(finalNumber)
                setOperationArray(tempOperationArray)
            }
        }
        else if(clickedButton === "AC"){
            setLastNumber(null)
            setOperationArray([])
            setResult(0)
        }
        else if(clickedButton === "back"){
            
            let tempOperationArray = [...operationArray];
            console.log(tempOperationArray)
            if(tempOperationArray.length){

                tempOperationArray.pop()

                if(lastNumber){
                    
                    let tempLastNumber = lastNumber;
                    tempLastNumber = tempLastNumber.substring(0, tempLastNumber.length-1);
                    if(tempLastNumber === "-" || tempLastNumber === "0" || !tempLastNumber.length){
                        // if last part left is -
                        setLastNumber(null)
                        setOperationArray(tempOperationArray)
                    }else{
                        // if number still left 
                        setLastNumber(tempLastNumber)
                        setOperationArray([...tempOperationArray, tempLastNumber])
                    }
                    
                }else{
                    let lastNumberAfterOperatorRemoved = tempOperationArray[tempOperationArray.length-1]
                    setLastNumber(lastNumberAfterOperatorRemoved)
                    setOperationArray(tempOperationArray)

                }
                
            }
        }
        buttonClicked.play();

    }   

    return (
        <div className={classes.Calculator}>
             <h1 className={classes.Head}>calculator</h1>
             <div className={classes.Body}>
                <div className={classes.ScreenSection}>
                    <div className={classes.AnswerS}>
                        {result ? result : 0}
                    </div>
                    <div ref={operationRef} className={classes.OperationS}>
                    {operationArray.map((ele, index)=>{
                        let style={
                            backgroundColor: "#636363",
                            padding: "3px"
                        }
                        if(isOperator(ele)){
                            style.padding = "3px 6px"
                        }
                        switch(ele){
                            case "+":
                                style.backgroundColor = "#2C7C5C"
                                break;
                            case "-":
                                style.backgroundColor = "#813C3C"
                                break;
                            case "Ã·":
                                style.backgroundColor = "#4BA0A2"
                                break;
                            case "Ã—":
                                style.backgroundColor = "#594BA2"
                                break;
                            case "%":
                                style.backgroundColor = "#964BA2"
                                break;
                        }
                        return <div key={index} style={style} className={classes.block}>{ele}</div>
                    })}
                    </div>
                </div>
                <div className={classes.ButtonSection}>
                    <Button buttonClicked={clickedHandler} grey red val="AC"/>
                    <Button buttonClicked={clickedHandler} grey val="%"/>
                    <Button buttonClicked={clickedHandler} grey backspace val="back"/>
                    <Button buttonClicked={clickedHandler} blue val="Ã·"/>
                    <Button buttonClicked={clickedHandler} val="1"/>
                    <Button buttonClicked={clickedHandler} val="2"/>
                    <Button buttonClicked={clickedHandler} val="3"/>
                    <Button buttonClicked={clickedHandler} blue val="Ã—"/>
                    <Button buttonClicked={clickedHandler} val="4"/>
                    <Button buttonClicked={clickedHandler} val="5"/>
                    <Button buttonClicked={clickedHandler} val="6"/>
                    <Button buttonClicked={clickedHandler} blue val="-"/>
                    <Button buttonClicked={clickedHandler} val="7"/>
                    <Button buttonClicked={clickedHandler} val="8"/>
                    <Button buttonClicked={clickedHandler} val="9"/>
                    <Button buttonClicked={clickedHandler} blue val="+"/>
                    <Button buttonClicked={clickedHandler} grey secondary val="+/-"/>
                    <Button buttonClicked={clickedHandler} val="0"/>
                    <Button buttonClicked={clickedHandler} grey secondary val="."/>
                    <Button buttonClicked={clickedHandler} grey green val="="/>
                </div>
             </div>
        </div>
    )
}

export default Calculator
