const ADD = "+";
const SUB = "-";
const MUL = "Ã—";
const DIV = "Ã·";
const PER = "%";
const PRECEDENCE = ["%", "Ã·", "Ã—", "+", "-"]

export function calculate(operationArray){
    //priority of operator
        //solve left and write with operation
            // return the resultat value to the calculate function 
    console.log(operationArray)
    // #DIV 
    if(isOperator(operationArray[0])){
        operationArray.shift()
    }
    let newResult = [...operationArray]
    let mainResult = 0;


    //priority
    // PER
    // DIV
    // MUL
    // ADD
    // SUB
    for(var i=0; i<PRECEDENCE.length; i++){

        if(operationArray.indexOf(PRECEDENCE[i]) !== -1){
            console.log("find", PRECEDENCE[i])
            let mainIndex = newResult.indexOf(PRECEDENCE[i])
            
            if(mainIndex+1 >= newResult.length){
                newResult.pop()
                return [...calculate(newResult), PRECEDENCE[i]]
            }else{
                
                // mainResult = (parseFloat(newResult[mainIndex-1])/parseFloat(newResult[mainIndex+1]))+""
                mainResult = basicOperation(parseFloat(newResult[mainIndex-1]), parseFloat(newResult[mainIndex+1]), PRECEDENCE[i])
                mainResult += ""
                console.log(mainResult)
                newResult.splice(mainIndex-1, 3);
                newResult.splice(mainIndex-1, 0, mainResult);
                console.log(newResult)
                if(newResult.length === 1){
                    console.log(mainResult)
                    return mainResult
                }
                return calculate(newResult)
            }
        }
        else if(PRECEDENCE[i] === PRECEDENCE[PRECEDENCE.length-1]){
            console.log("Hello", operationArray)
            return operationArray[0]
        }

    }

}

function basicOperation(numA, numB, operation){
    switch (operation){
        case ADD:
            return numA+numB;
        case SUB:
            return numA-numB;
        case DIV:
            return numA/numB;
        case MUL:
            return numA*numB;        
        case PER:
            return numA*numB/100;        
    }
}

export function isOperator(value){
    switch(value){
        case ADD:
            return true;
        case SUB:
            return true;
        case DIV:
            return true;
        case MUL:
            return true;
        case PER:
            return true;
        default:
            return false;
    }
}